            ' 1st' : '1',
            ' 2nd' : '2',
            ' 3rd' : '3',
            ' 4th' : '4',
            ' 5th' : '5',
            ' 6th' : '6',
            ' 7th' : '7',
            ' 8th' : '8',
            ' 9th' : '9',
            'Now batting' : '',
            'way outside' : '',
            'outside' : '',
            'inside' : '',
            'way inside' : '',
            'way low' : '',
            'low' : '',
            'high' : '',
            'way high' : '',
            'down the middle' : '',
            'first baseman': '',
            'second baseman': '',
            'third baseman': '',
            'shortstop': '',
            'pitcher': '',
            'catcher': '',
            'left fielder': '',
            'center fielder': '',
            'right fielder': '',
            'Strike.' : '',
            'Ball.' : '',
            'Fouled off.': '',
            'In play.': '',
            'Swinging strike.': '',
            '4-seam': '',
            '2-seam': '',
            'slider': '',
            'fork': '',
            'cutter': '',
            'sinker': '',
            'curve': '',
            'change': '',
            ' struck out looking.': '',
            ' walked.': '',
            ' struck out swinging.': '',
            'Previous: ': '',
            'looks like: ': '',
            'breaking ball': '',
            'fastball': '',
            'Batting, ': '',
            'Catching, pitch selection': '',
            'Season': '',
            'Game': '',
            'Pitch': '',
            'Control': '',
            'Velocity': '',
            'Break': '',
            'At Bat :': '',
            'On Deck :': '',
            'Eye :': '',
            'Power :': '',
            'Speed :': '',
            'Up to Bat': '',
            'Fielding': '',
            'BA' : '',
            'OBP' : '',
            'OPS' : '',
            'PA' : '',
            'H/2B/3B/HR' : '',
            'H' : '',
            '2B' : '',
            '3B' : '',
            'HR' : '',
            'RBI' : '',
            'R' : '',
            'BB' : '',
            'SO' : '',

            'Select Language:' : '',
            'Run Fast Simulation' : '',
            'Play Ball!' : '',
            'Spectate the CPU': 'CPU',

            'Throws/Bats' : ' ',
            'LHP' : '',
            'RHP' : '',
            'LHB' : '',
            'RHB' : '',
            'L' : '',
            'R ' : '',
